import { BrowserRouter as Router, Link, Route, Switch } from "react-router-dom";

import "./App.css";

const Tab1 = () => <div>Tab1</div>;
const Tab2 = () => <div>Tab2</div>;

function App() {
  return (
    <div className="App">
      <Router>
        <div>
          <Link to="/tab1">Tab1</Link>
          <Link to="/tab2">Tab2</Link>
        </div>
        <Switch>
          <Route path="/tab1">
            <Tab1 />
          </Route>
          <Route path="/tab2">
            <Tab2 />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
